import { letterFrequency } from '@visx/mock-data';
import { Group } from '@visx/group';
import { Circle } from '@visx/shape';
import { scaleOrdinal } from '@visx/scale';
import { LegendOrdinal, LegendItem, LegendLabel } from '@visx/legend';

const data = letterFrequency;

const width = 500;
const height = 500;
const margin = {top: 20, bottom: 20, left: 20, right: 20};

const colorswatchWidth = 10;
const labelColumnWidth = 16;
const legendGutter = 5;
const labelGutter = 4;

const x = d => d.letter;
const y = d => +d.frequency * 100;

const gridHeight = 20; // in dots
const gridWidth = 20;
const dotPadding = 2;


const colorScale = scaleOrdinal({
  range: ['#ff0000', '#00ff00', '#0000ff', '#cc0000', '#00cc00', '#0000cc', '#aa0000', '#00aa00', '#0000aa', '#880000', '#008800', '#000088','#440000', '#004400', '#000044', '#aaaa00', '#aaaa00', '#00aaaa', '#888800', '#008888', '#880088','#440044', '#444400', '#004444', '#224400', '#002244'],
  domain: data.map(x),
});

const compose = (scale, accessor) => data => scale(accessor(data));
  
// sum all counts and determine percentage share for each category
const getTotal = (data) => data.reduce((acc, curr) => acc + curr.frequency ,0);
const total = getTotal(data); // tally counts in data
const getDotCount = (number, total) => Math.round((number / total) * gridWidth * gridHeight);
const dotCounts = data.map((d) => {
  return {
    dotCount: getDotCount(d.frequency, total),
    category: d.letter,
  }
});

// create an array of data for each dot
const dotArray = dotCounts.reduce((acc, curr) => {
  const arr = Array(curr.dotCount);
  arr.fill({category: curr.category});
  return acc.concat(arr);
}, []);

// functions to position dots in the grid
const dotCenterX = (i, max) => margin.top * 2 + ((i % gridWidth) * (max/gridWidth));
const dotCenterY = (i, max) => margin.left * 2 + (Math.floor(i / gridWidth) * (max/gridHeight));

const dotColor = compose(colorScale, d => d.category);

const getDotColor = (d, i, progress) => (Math.ceil(progress * dotArray.length) > i) ? dotColor(d) : '#ffffff';

const currentCategory = (progress) => {
  const currentIndex = Math.round(progress * dotArray.length);
  return (currentIndex < dotArray.length) ? dotArray[currentIndex].category : dotArray[dotArray.length - 1].category;
  
}

const DotMatrix = function({progress, screenWidth, screenHeight}) {
  // map data to <Circle /> components
  
  const availableWidth = screenWidth - 10 - 16 - 5 - 16 - 4;
  const xMax = (width < availableWidth ? width : availableWidth)  - margin.left - margin.right;
  const yMax = (height < screenHeight ? width : screenHeight) - margin.top - margin.bottom;

  const dots = dotArray.map((d, i) => {
    return (
      <Group key={`dot${i}`}>
        <Circle r={(xMax / gridWidth / 2) - dotPadding } cx={dotCenterX(i, xMax)} cy={dotCenterY(i, yMax)} fill={getDotColor(d, i, progress)} />
      </Group>
    );
  })

  return (
    <>
    <svg width={width < screenWidth ? width : screenWidth} height={height < screenHeight ? height : screenHeight}>
      {dots}
    </svg>
    <LegendOrdinal scale={colorScale}  labelFormat={(label) => `${label.toUpperCase()}`}>
      {(labels) => (
        <div style={{ display: 'flex', flexDirection: 'column', fontSize: '16px', paddingLeft: '16px' }}>
          {labels.map((label, i) => (
            <LegendItem
              key={`legend-quantile-${i}`}
              margin={`0 ${legendGutter}px`}
            >
              <svg width={colorswatchWidth} height={colorswatchWidth}>
                <rect fill={label.value} width={colorswatchWidth} height={colorswatchWidth} />
              </svg>
              <LegendLabel align="left" margin={`0 0 0 ${labelGutter}px`}>
                <div style={{width: `${labelColumnWidth}px`}} className={currentCategory(progress) === label.text ? 'font-bold' : ''}>{label.text}</div>
              </LegendLabel>
            </LegendItem>
          ))}
        </div>
      )}
    </LegendOrdinal>
    </>
  );
}

export default DotMatrix;