import { useMemo } from 'react';

const ProgressIndicator = function({progress}){

  const percentage = useMemo(() => Math.round(progress * 100), [progress]);

  return <div className="text-center text-white bg-black h-screen w-screen flex items-center justify-center">
    <div className="inline-block text-5xl">{percentage}%</div>
  </div>
}

export default ProgressIndicator;