import logo from './logo.svg';
import './App.css';
import { Controller, Scene } from 'react-scrollmagic';
import ProgressIndicator from './components/ProgressIndicator';
import DotMatrix from './components/DotMatrix';
import MyMap from './components/MyMap';
import { withScreenSize, withParentSize } from '@visx/responsive';

function App() {
  const DotMatrixAdaptive = withScreenSize(DotMatrix);
  const MyMapAdaptive = withParentSize(MyMap);
  return (
    <div>
      <div className='h-screen bg-gray-600'>
        <h1 className='text-4xl text-center py-16'>
          Welcome Text
        </h1>
      </div>
      <Controller>
        <Scene duration={0}>
          <div className="h-screen w-screen">
            <h1 className="text-4xl text-center py-16">NYC Police Precincts</h1>
            <div className="flex items-center justify-center flex-row">
              <MyMapAdaptive />
            </div>
          </div>
        </Scene>
        <Scene duration={1200} triggerHook="onLeave" className="bg-gray-300" pin>
          {
            (progress) => (
              <div>
                <ProgressIndicator progress={progress} />
              </div>
            )
          }
        </Scene>
        <Scene duration={1200} triggerHook="onLeave" pin>
          {
            (progress) => (
              <div className='min-h-screen w-screen'>
                <h1 className='text-4xl text-center pt-16'>Letter Frequency in English</h1>
                <div className='flex items-center justify-center flex-row'><DotMatrixAdaptive progress={progress} /></div>
              </div>
            )
          }
        </Scene>
        <Scene duration={1200} triggerHook="onLeave" className="bg-gray-300" pin>
          {
            (progress) => (
              <div>
                <ProgressIndicator progress={progress} />
              </div>
            )
          }
        </Scene>
      </Controller>
      <div className='h-screen bg-gray-600'></div>
    </div>
  );
}

export default App;
